<template>
	<div class="box nodalDividend">
		<nav-bar :title="$t('receiveDividend')" />
		<div class="navBox nodalDividendBox">
			<div class="getHelpDetails">
				<div class="reveiceIncome">
					<span>{{ rewardPool }}</span>
					<div>{{ $t('profitPool') }}</div>
				</div>
				<div class="orderInfo">
					<div>
						<span>{{ $t('nextDividend') }}</span>
						<span>{{ time }}</span>
					</div>
					<span></span>
					<div>
						<span>{{ $t('totalDividend') }}</span>
						<span>{{ totalReward }}<span>USDT</span></span>
					</div>
				</div>
				<div class="incomeBg"></div>
			</div>
			<div class="navBox-record" v-if="list.length > 0">
				<div v-for="(item, index) in list" :key="index">
					<div>
						<span>{{ $t('nodeDividend') }}</span>
						<span>{{ item.date }}</span>
					</div>
					<div>
						<span>+{{ item.amount }} USDT</span>
					</div>
				</div>
				<not-record v-if="!list.length" :text="NoRevenueRecord"></not-record>
			</div>
		</div>
	</div>
</template>

<script>
	import { Button , Toast } from 'vant';
	import navBar from '@/components/nav-bar';
	
	export default{
		components:{ navBar , Button },
		data(){
			return {
				rewardPool: 0,
				totalReward: 0,
				time: '',
				list: [
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' }
				],
				NoRevenueRecord: this.$t('NoRevenueRecord')
			}
		},
		methods:{
			async init() {
				//分红池
				this.$tronWeb.call({
					contractAddress: this.$contracts.Pool.address,
					method: 'availTotalAmouns(uint256)',
					parameter: [{ type: 'uint256', value: 3 }]
				}).then(res => {
					this.rewardPool = res.constant_result[0].decode(['uint256']).toString().fromSun();
				})

				//下次分红时间
				this.$contracts.Noder.latestBonusTime().call().then(res => {
					const current = res.toNumber() || new Date().getTime() / 1000;
					let time = new Date((current + (86400 * 7)) * 1000),
						year = time.getFullYear(),
						month = time.getMonth() + 1,
						day = time.getDate();

					this.time = `${year}/${month}/${day}`;
				})

				//累计分红
				this.$contracts.Noder.totalReward(this.$address).call().then(res => {
					this.totalReward = res.toString().fromSun();
				})

				const block = await this.$tronWeb.getBlockNumber();
				const blockNumber = block < 100000 ? 0 : block - 100000;
				const types = [this.$t('expand'), this.$t('Executives'), this.$t('manager'), this.$t('culture')]

				this.$tronWeb.event({
					contractAddress: this.$contracts.Noder.address,
					method: 'Log_Bounds'
				}).then(event => {
					console.log(event)
					if(!event.length) return;

					event.forEach(async item => {
						if (window.tronWeb.address.fromHex(item.result.owner) === this.$address) {
							let name = (await this.$tronWeb.call({
										contractAddress: this.$contracts.Relations.address,
										method: 'addressToNickName(address)',
										issuerAddress: this.$address,
										parameter: [{ type: "address", value: this.$address }]
									}).then()).constant_result[0].decode(['bytes16']).hexToString(true)
									
							this.list.push({
								time: item.result.time,
								amount: item.result.amount.fromSun(),
								type: types[Number(item.result.atype)],
								date: this.$toTime(item.result.time,'Y-M-D H:i'),
								name: name
							})
							
							this.list.sort((a,b) => {
								return b.time - a.time
							})
						}
					})

					this.list.sort(function(a,b){
						return b.time - a.time;
					})
				})
			},
			pushCompensateRecord(){
				this.App_Push('/nodalDividendDetail')
			},
		},
		created(){
			this.$tronWeb && this.init();
		}
	}
</script>

